import Vue from 'vue';
import Router from 'vue-router';

/* layout*/
import Layout from '../views/layout/Layout';

// +
import pointList from '../views/points/list';
import ponintAdd from '../views/points/add';
import radioList from '../views/radio/list';
import radioAdd from '../views/radio/add';

/* error page*/
const Err404 = resolve => require(['../views/error/404'], resolve);
const Err401 = resolve => require(['../views/error/401'], resolve);

/* login*/
import Login from '../views/login/';
import authRedirect from '../views/login/authredirect';
import sendPWD from '../views/login/sendpwd';
import reset from '../views/login/reset';
import behavior from '../views/behavior/index';
import behaviorAdd from '../views/behavior/behaviorAdd';

import userinfo from '../views/user/userinfo';
import userInfoDetail from '../views/user/userInfoDetail';


const Index = resolve => require(['../views/index/index'], resolve);

// /* Task Manage*/
const Task = resolve => require(['../views/task/index'], resolve);
const TaskAdd = resolve => require(['../views/task/taskAdd'], resolve);
const TaskSettings = resolve => require(['../views/task/taskSettings'], resolve);
const TaskSettingsModify = resolve => require(['../views/task/taskSettingsModify'], resolve);

// /* Task View*/
// const SubTaskView = resolve => require(['../views/taskView/subTaskView'], resolve);
// const DutrationView = resolve => require(['../views/taskView/durationView'], resolve);
// const LogView = resolve => require(['../views/taskView/logView'], resolve);
// const TreeView = resolve => require(['../views/taskView/treeView'], resolve);

/* Introduction*/
const Introduction = resolve => require(['../views/introduction/index'], resolve);

/* charts*/
const LineMarker = resolve => require(['../views/charts/line'], resolve);
const MixChart = resolve => require(['../views/charts/mixchart'], resolve);


/* error log*/
// const ErrorLog = resolve => require(['../views/errlog/index'], resolve);

/* icon doc*/
const IconDoc = resolve => require(['../views/iconDoc/index'], resolve);

/* permission */
const Permission = resolve => require(['../views/permission/index'], resolve);

/* basic credit */
const basicCredit = resolve => require(['../views/basicCredit/index'], resolve);

const basicUpdate = resolve => require(['../views/basicCredit/update'], resolve);

const businessCredit = resolve => require(['../views/basicCredit/businessCredit'], resolve);

const creditEdit = resolve => require(['../views/basicCredit/creditEdit'], resolve);

const creditSign = resolve => require(['../views/basicCredit/creditSign'], resolve);

const signUpdate = resolve => require(['../views/basicCredit/signUpdate'], resolve);

Vue.use(Router);

/**
 * icon :the icon show in the sidebar
 * hidden: if hiddenLtrue will not show in the sidebar
 * redirect :if redirect:noredirect will not redirct in the levelbar
 * noDropdown : if noDropdown:true will not has submenu
 * meta: { role: ['admin'] }  will control the page role
 */
export default new Router({
    // mode: 'history', //
    scrollBehavior: () => ({
        y: 0
    }),
    routes: [{
            path: '/login',
            component: Login,
            hidden: true
        }, {
            path: '/behavior',
            component: behavior,
            hidden: true
        }, {
            path: '/behaviorAdd',
            component: behaviorAdd,
            hidden: true
        }, {
            path: '/userInfoDetail',
            component: userInfoDetail,
            hidden: true
        }, {
            path: '/authredirect',
            component: authRedirect,
            hidden: true
        }, {
            path: '/sendpwd',
            component: sendPWD,
            hidden: true
        }, {
            path: '/reset',
            component: reset,
            hidden: true
        }, {
            path: '/404',
            component: Err404,
            hidden: true
        }, {
            path: '/basic-credit',
            component: basicCredit,
            hidden: true
        }, {
            path: '/basic-update',
            component: basicUpdate,
            hidden: true
        }, {
            path: '/401',
            component: Err401,
            hidden: true
        }, {
            path: '/business-credit',
            component: businessCredit,
            hidden: true
        }, {
            path: '/credit-edit',
            component: creditEdit,
            hidden: true
        }, {
            path: '/credit-sign',
            component: creditSign,
            hidden: true
        }, {
            path: '/sign-update',
            component: signUpdate,
            hidden: true
        },
        // { path: '/logView/:taskId', name: 'logView', component: LogView},
        {
            path: '/',
            component: Layout,
            redirect: '/',
            name: '',
            hidden: true,
            children: [{
                path: '',
                component: Index
            }]
        }, {
            path: '/userinfo',
            component: userinfo,
            hidden: true

        }, {
            path: '/task/index',
            component: Task,
            hidden: true

        }, {
            path: '/task/taskAdd',
            component: TaskAdd,
            hidden: true

        }, {
            path: '/task/taskSettings',
            component: TaskSettings,
            hidden: true

        }, {
            path: '/task/taskSettingsModify',
            component: TaskSettingsModify,
            hidden: true

        },

        // {
        //   path: '',
        //   component: Layout,
        //   redirect: '/manage/taskManage',
        //   icon: 'home',
        //   noDropdown: true,
        //   meta: { role: ['admin','editor','developer'] },
        //   hidden:true,
        //   children: [{ path: 'index', component: Index, name: '',  }]
        // },
        // {
        //   path: '/view',
        //   component: Layout,
        //   redirect: '/view/subTaskView',
        //   name: '',
        //   icon: 'activity',
        //   meta: { role: ['admin'] },
        //   children: [
        //       { path: 'subTaskView', component: SubTaskView, name: '' },
        //       { path: 'treeView/:taskId/:wfId', component: TreeView, name: 'Tree', hidden: true },
        //       { path: 'dutrationView/:taskId/:wfId', component: DutrationView, name: 'Dutration', hidden: true },
        //       { path: 'logView/:taskId/:wfId', component: LogView, name: 'Log', hidden:true }
        //   ]
        // }
        ,
        // {
        //   path: '/permission',
        //   component: Layout,
        //   redirect: '/permission/index',
        //   name: '',
        //   icon: 'lock',
        //   meta: { role: ['admin'] },
        //   noDropdown: true,
        //   children: [{ path: 'index', component: Permission, name: '', meta: { role: ['admin'] } }]
        // },
        // {
        //   path: '/charts',
        //   component: Layout,
        //   redirect: '/charts/line',
        //   name: '',
        //   icon: 'chart-bars',
        //   children: [
        //       { path: 'line', component: LineMarker, name: '' },
        //       { path: 'mixchart', component: MixChart, name: '' }
        //   ]
        // },
        // {
        //   path: '/errorpage',
        //   component: Layout,
        //   redirect: 'noredirect',
        //   name: '',
        //   icon: 'warning',
        //   children: [
        //       { path: '401', component: Err401, name: '401' },
        //       { path: '404', component: Err404, name: '404' }
        //   ]
        // },
        {
            path: '/icon',
            component: Layout,
            redirect: 'noredirect',
            name: '',
            icon: 'diamond',
            noDropdown: true,
            children: [{
                path: 'icon',
                component: IconDoc,
                name: ''
            }]
        },
        // {
        //   path: '/errlog',
        //   component: Layout,
        //   redirect: 'noredirect',
        //   name: 'errlog',
        //   icon: 'bug',
        //   noDropdown: true,
        //   children: [{ path: 'log', component: ErrorLog, name: '' }]
        // },
        {
            path: '/point/list',
            component: pointList
        }, {
            path: '/point/add',
            component: ponintAdd
        }, {
            path: '/radio/list',
            component: radioList
        }, {
            path: '/radio/add',
            component: radioAdd
        },


        {
            path: '*',
            redirect: '/404',
            hidden: true
        }
    ]
});


