<template>
	<div class="userInfoDetail">
		<div class="detail_list">
		  <ul>
		  	<li><span>ID<i>{{list.userId}}</i></span></li>
		  	<li><span><i>{{list.ratio}}</i></span></li>
		  	<li><span><i>{{list.totalScore}}</i></span></li>
		  	<li><span><i>{{list.creditScore}}</i></span></li>
		  	<li>
		  		<span class="detail_item"><i>{{list.addScore}}</i></span>
		  		<span class="detail_item"><i>{{list.addScoreFre}}</i></span>
		  		<span class="detail_item"><i>{{list.subScoreFre}}</i></span>
		  	</li>
		  	<li>
		  		<span class="detail_item"><i>{{list.adjustScore}}</i></span>
		  		<span class="detail_item"><i>{{list.adjustAddScoreFre}}</i></span>
		  		<span class="detail_item"><i>{{list.adjustSubScoreFre}}</i></span>
		  	</li>
		  </ul>
		</div>

		<div>
			<el-table :data="detail" border style="width: 100%">
        <el-table-column prop="busName" label="" width="180"></el-table-column>
        <el-table-column prop="busId" label="id" width="180"></el-table-column>
        <el-table-column prop="score" label="" width="80"></el-table-column>
        <el-table-column prop="adjustTime" label="" width="240"></el-table-column>
        <el-table-column prop="validStartTime" label="" width="180"></el-table-column>
        <el-table-column prop="validEndTime" label="" width="180"></el-table-column>
        <el-table-column prop="status" label="" width="180"></el-table-column>
        <el-table-column prop="memo" label="" width="180"></el-table-column>
        <el-table-column prop="operator" label="" width="180"></el-table-column>
      </el-table>
		</div>

		<div v-show="!listLoading" class="pagination-container">
      <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="listQuery.page" :page-sizes="[5,10,20,30,50]"
        :page-size="listQuery.rows" layout="total, sizes, prev, pager, next, jumper" :total="total">
      </el-pagination>
    </div>

	</div>
</template>

<script>
	import { infoList , infoUp , infoDown} from 'api/behavior';

	export default {
		name: 'userInfoDetail',
		data(){
			return {
				list:{
					"userId": '',
					"ratio":"",
					"totalScore":'',
					"creditScore":'',
					"addScore":'',
					"addScoreFre": '',
					"subScoreFre":'',
					"adjustScore":'',
					"adjustAddScoreFre":'',
					"adjustSubScoreFre":''
				},
				detail:[],
				listLoading: true,
				listQuery: {
	        page: 1,
	        rows: 20,
	        keyword: ''
	      },
	      total: null,
	      userId: '',
			}
		},
		created(){
			this.getList();
		},
		methods:{
			getList() {
				this.userId = this.$route.query.userId;
				infoUp(this.userId).then(response => {
	        if (response.success) {
	          this.list = response.data;
	          this.list.adjustScore = Number(this.list.adjustScore).toFixed(2);
	          this.list.ratio = Number(this.list.ratio)*100+"%";
	        } else {
	          this.$notify({
	            title: '',
	            message: response.message,
	            type: 'error',
	            duration: 2000
	          })
	        }
	      })

				infoDown(this.userId,this.listQuery).then(response => {
	        if (response.success) {

	          this.total = response.data.total;
	          this.detail = response.data.items;
	          this.listLoading = false;
	        } else {
	          this.$notify({
	            title: '',
	            message: response.message,
	            type: 'error',
	            duration: 2000
	          })
	        }
	      });
			},
			handleSizeChange(val) {
	      this.listQuery.rows = val;
	      this.getList();
	    },
	    handleCurrentChange(val) {
	      this.listQuery.page = val;
	      this.getList();
	    }
		}
	}
</script>

<style type="text/css">
	.userInfoDetail{
		padding-left: 20px;
	}
	.userInfoDetail .detail_list ul{
		list-style: none;
		padding: 0;
		margin: 40px 0;
	}
	.userInfoDetail .detail_list ul li{
		list-style: none;
		padding: 0;
		margin: 0;
		line-height: 36px;
	}
	.userInfoDetail .detail_list .detail_item{
		display: inline-block;
		margin-right: 60px;
	}
	.userInfoDetail .detail_list i{
		display: inline-block;
		font-style: normal;
		color: #20a0ff;
		margin-left: 10px;
	}
</style>


