<template>
<div class="container-form">
    
    <el-form ref="form" :rules="rules"  :model="form" label-width="100px">
      <el-form-item label=":" prop="listName" >
        <el-input v-model="form.listName" ></el-input>
      </el-form-item>
      <ul class="form-add-row">
          <li>
              <div class="el-form-item">
                  
              </div>
               <div class="el-form-item">
                  
              </div>
               <div class="el-form-item">
                  
              </div>
               <div class="el-form-item">
                  
              </div>
          </li>
          <li v-for="(item,index) in form.items" :key="index" >
                <div class="el-form-item">
                  {{index+1}}
                </div>
                <el-form-item :prop="'items.'+index+'.startScore'" :rules="numberRules"  >
                <!-- <el-form-item> -->
                    <el-input :disabled="index>0?true:false" v-model="item.startScore" ></el-input>
                </el-form-item>
                <el-form-item :prop="'items.'+index+'.endScore'"  :rules="numberRules" >
                <!-- <el-form-item> -->
                    <el-input v-model="item.endScore" @blur="change(index)"  ></el-input>
                </el-form-item>
                <el-form-item :prop="'items.'+index+'.ratio'" :rules="numberRules" >
                <!-- <el-form-item> -->
                    <el-input v-model="item.ratio"  ></el-input>
                </el-form-item>
          </li>
      </ul>
       <div class="row">
           <el-button @click="removeRow"> </el-button>
              <el-button type="info" @click="addRow" > </el-button>
      </div>
       <div class="row">
              <el-button @click="submit" type="primary" > </el-button>
      </div>
      
      
    </el-form>
</div>
</template>
<script>
import { fetch } from "utils/fetch";
let getYMD = (time=new Date())=>{
    console.log(time.getFullYear()+"-"+(Number(time.getMonth())+1)+"-"+time.getDate())
    return time.getFullYear()+"-"+formateTime((Number(time.getMonth())+1))+"-"+formateTime(time.getDate())+" "+formateTime(time.getHours())+":"+formateTime(time.getMinutes())+":"+formateTime(time.getSeconds());
}
let formateTime = (value)=>{
    return value<10?("0"+value):value
}

let getData = async (url, method = "get", data = {}) => {
  let result = false;
  try {
    result = await fetch({
      url,
      method,
      data
    });
  } catch (err) {
    result = false;
  }
  return result;
};

// const toExcString = function (array, type = {
//     ":": "=",
//     ",": "&"
// }) {

//     let result = "";
//     for (var temp in array) {
//         result += temp + '=' + encodeURI(array[temp]) + "&"
//     }
//     return result.substring(-1, result.length - 1);
// }

export default {
  methods: {
    addRow: function() {
      let item = this.form.items[this.form.items.length - 1];
      // debugger
      this.form.items.push({
        startScore: item.endScore == "" ? "" : (Number(item.endScore) + 0.01).toString(),
        endScore: "",
        ratio: ""
      });
    },
    removeRow: function() {
      if (this.form.items.length <= 1) {
        return;
      }
      this.form.items.pop();
    },
    change: function(_index) {
      this.form.items.map((value, index) => {
        if (index > _index && index > 0) {
          this.form.items[index].startScore =
            this.form.items[index - 1] == ""
              ? ""
              : (Number(this.form.items[index - 1].endScore) + 0.01).toString();
        }
      });
      console.log(_index);
    },
    submit: async function() {
       let k = {};
      let pro = new Promise((rev, reb) => {
        k.rev = rev;
        k.reb = reb;
      });
      this.$refs['form'].validate(valid => {
        if (valid) {
          k.rev(true);
        } else {
          console.log("error submit!!");
          k.rev(false);
        }
      });

      let info = await pro;
      if (!info) {
        return;
        //  console.log(info)
      }


      // let result = true;
      let sendData =  this.form.items.map((value, index) => {
          return Object.assign({},{status:false,createTime:getYMD(),listName:this.form.listName},value);
      });
      
      // let sendData = Object.assign({},this.form,{
      //   items
      // })
      
      // if (!result||this.form.listName=="") {
      //       this.$alert("", "");
      //       return false;
      // }
      // debugger
      this.sendData(sendData);
    },
    sendData:async function(data){
        this.loading = true;
        let result = await getData("/confratio/addConfRatio.do","post",data);
        this.loading = false;
        if(result.success===true){
            //
            this.$alert('', "");
            this.$router.push('/radio/list');
        }else{
            this.$alert((result&&result.data)||"GG", "");
        }
    }
  },
  data() {
    return {
        loading:false,
      form: {
        listName: "",
        items: [
          {
            startScore: "",
            endScore: "",
            ratio: ""
          }
        ]
      },
      numberRules:[
            {required: true, message: '', trigger: 'blur'},
            { message: "", pattern: /^[0-9.]+$/, trigger: "change" },
            { message: "", pattern:  /^\d+(\.\d{2})?$/, trigger: "blur" }
        ],
      rules: {
        listName: [{ required: true, message: "", trigger: "blur" }]
      },
      
    };
  }
};
</script>
<style lang="scss">
$borderColor: #bfcbd9;
.form-add-row {
  list-style-type: none;
  margin: 0;
  padding: 0;

  li {
    display: flex;
    flex-wrap: nowrap;
    &:first-of-type {
      .el-form-item {
        // padding-bottom: 0;
        padding: 5px;
      }
    }
    .el-form-item {
      border-bottom: none;
    }
    &:last-of-type {
      .el-form-item {
        border-bottom: 1px solid $borderColor;
      }
    }
    // }
  }
  .el-form-item {
    margin-bottom: 0 !important;
    // padding-bottom: 20px;
    padding: 15px;
    width: 200px;
    border: 1px solid $borderColor;
    text-align: center;
    justify-content: center;
    align-items: center;
    display: flex;
    border-right: none;
    &:last-of-type {
      border-right: 1px solid $borderColor;
    }
  }
  .el-form-item__content {
    margin-left: 0 !important;
  }
  input {
    text-align: center;
    border: none;
  }
}
.row {
  margin-top: 20px;
}
.container-form {
  display: flex;
  // flex-direction: column;
  padding: 10px 20px 5px;
  background-color: #fff;
  form {
    min-width: 38%;
  }
}
</style>






