<template>
  <div class="credit-container">
    <div>
      <el-form ref="form" :model="form" :rules="rules" label-width="110px" class="form">
        <el-form-item label="" prop="busName">
          <el-input v-model="form.busName"></el-input>
        </el-form-item>
        <el-form-item label="" prop="title">
          <el-input v-model="form.title"></el-input>
        </el-form-item>
        <el-form-item label="" prop="subTitle">
          <el-input v-model="form.subTitle  "></el-input>
        </el-form-item>
        <el-form-item label="" required>
          <img v-bind:src="form.imageUrl" class="thumbnail"/>
          <div id="container">
            <el-button size="small" type="primary" id="pickfiles"></el-button>
          </div>
        </el-form-item> 
        <el-form-item label="" class="url_links" required>
          <template>
            <el-radio-group v-model="radio2" class="link_list" @change="radioChange">
              <el-radio :label="0" ></el-radio>
              <el-radio :label="1" >h5</el-radio>
              <el-radio :label="2" >native</el-radio>
            </el-radio-group>
          </template>

          <div class="links_group">
            <div v-show="!native_group" class="native_group">
              <span class="native_group_item">
                <i>IOS</i>
                <el-input v-model="classNameIos"></el-input>
              </span>
              <span class="native_group_item">
                <i>Android</i>
                <el-input v-model="classNameAndrow  "></el-input>
              </span>
              <span class="native_group_item">
                <i>URL</i>
                <el-input v-model="h5_LinkUrl"></el-input>
              </span>
            </div>
            <span class="link_h5" v-show="!h5_group">
              <el-input v-model="form.linkUrl"></el-input>
              <span>url</span>
            </span>
          </div>
        </el-form-item> 
        <el-form-item>  
          <el-button type="primary" @click="commitAdd"></el-button>
        </el-form-item> 
      </el-form>
    </div>
  </div>
</template>

<script>
import { save} from 'api/behavior';
import { uploadQiNiu} from 'utils/uploadQiNiu';
export default {
  name: 'userinfo',
  data() {
    return {
      list: null,
      total: null,
      editorDialogVisable: false,
      listLoading: true,
      uploadSign: true,
      h5_LinkUrl:'',
      radio2:0,
      form:{
        busName: '',
        title: '',
        subTitle:'',
        imageUrl:'',
        linkUrl:'',
        className:'',
        classType:0
      },
      classNameIos:'',
      classNameAndrow:'',
      native_group:true,
      h5_group:true,
      rules: {
        busName: [
          { required: true, message: '', trigger: 'blur' },
          { min: 0, max: 100, message: ' 3  5 ', trigger: 'blur' }
        ],
        title: [
           { required: true, message: '', trigger: 'blur' },
           { min: 0, max: 100, message: ' 3  5 ', trigger: 'blur' }
        ],
        subTitle: [
          { required: true, message: '', trigger: 'blur' },
           { min: 0, max: 100, message: ' 3  5 ', trigger: 'blur' }
        ]
        
      }//
    }
  },
  created() {
    if(this.uploadSign){
      this.uploadSign = false;
      var _this = this;
      setTimeout(function(){
        uploadQiNiu(_this);
      },500);
    }
  },
  methods:{
    getList() {},
    radioChange(val){
      if(val == 0){
        this.h5_group = true;
        this.native_group = true;
        this.form.classType = val;

        this.classNameIos='';
        this.classNameAndrow='';
        this.h5_LinkUrl='';
        this.form.linkUrl = '';

      }
      if(val == 1){
        this.h5_group = false;
        this.native_group = true;
        this.form.classType = val;

        this.classNameIos='';
        this.classNameAndrow='';
        this.h5_LinkUrl='';

      }
      if(val == 2){
        this.native_group = false;
        this.h5_group = true;
        this.form.classType = val;

        this.form.linkUrl = '';
      }
    },
    commitAdd(){

      if(this.radio2 == '2'){ 
        this.form.linkUrl = this.h5_LinkUrl;
      }
      
      this.form.className= this.classNameIos+','+this.classNameAndrow;
      if(this.radio2 == 0 ){
        this.form.className='';
        this.form.linkUrl=' ';
        this.form.classType=0;
      }
      if(this.radio2 == 1 ){
        this.form.className='';

        this.form.classType=1;

        if(this.form.linkUrl == '' ){
          this.$notify({
            title: '',
            message: 'h5',
            type: 'error',
            duration: 2000
          });
          return false;
        }

      }
      if(this.radio2 == 2 ){        
        this.form.classType=2;

        if(this.classNameIos == '' ){
          this.$notify({
            title: '',
            message: '',
            type: 'error',
            duration: 2000
          });
          return false;
        }

        if(this.classNameAndrow == '' ){
          this.$notify({
            title: '',
            message: '',
            type: 'error',
            duration: 2000
          });
          return false;
        }

        // if(this.h5_LinkUrl == '' ){
        //   this.$notify({
        //     title: '',
        //     message: '',
        //     type: 'error',
        //     duration: 2000
        //   });
        //   return false;
        // }
      }


      if(this.form.busName == ""){
          this.$notify({
            title: '',
            message: '',
            type: 'error',
            duration: 2000
          });
          return false;
        }
        if(this.form.title == ""){
          this.$notify({
            title: '',
            message: '',
            type: 'error',
            duration: 2000
          });
          return false;
        }
        if(this.form.subTitle == ""){
          this.$notify({
            title: '',
            message: '',
            type: 'error',
            duration: 2000
          });
          return false;
        }
        if(this.form.imageUrl == ""){
          this.$notify({
            title: '',
            message: '',
            type: 'error',
            duration: 2000
          });
          return false;
        }
        if(this.form.classType === null){
          this.$notify({
            title: '',
            message: '',
            type: 'error',
            duration: 2000
          });
          return false;
        }

      save(this.form).then(response => {

        if (response.success) {
          this.$notify({
            title: '',
            message: '',
            type: 'success',
            duration: 2000
          });
          this.editorDialogVisable = false;
          this.h5_LinkUrl='';
          this.classNameIos='';
          this.classNameAndrow='';
          this.form = {
            busName: '',
            title: '',
            subTitle:'',
            imageUrl:'',
            linkUrl:'',
            className:''
          };
          
        } else {
          this.$notify({
            title: '',
            message: response.message,
            type: 'error',
            duration: 2000
          });
        }
      })
    }
  }
}
</script>

<style lang="scss">
  @import "src/styles/mixin.scss";

  .credit-container {
    padding: 20px;
  }
  .credit-container .form .el-input{
    width:500px;
  }

  .links_group{
    padding-left:40px;
    height:168px;
  }

  .link_list .el-radio{
    margin-right:10px;
    display:inline-block;
  }

  .link_h5{
    display:block;
    padding:10px 0;
  }

  .native_group{
    display:block;
    
  }
  .native_group .native_group_item{
    display:block;
    padding:10px 0;
  }

  .native_group .native_group_item i{
    display:inline-block;
    width:60px;
    font-style:normal;
  }
</style>




