<template>
<div class="container-list-pagination">
    <el-pagination
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page="dataInfo.page"
      :page-sizes="[10, 20, 50, 200]"
      :page-size="dataInfo.size"
      layout="total, sizes, prev, pager, next, jumper"
      :total="dataInfo.total">
    </el-pagination>
</div>
</template>
<script>
let defaultData = {
  page: 1,
  size: 10,
  total: 1
};
export default {
    props:{
        dataInfo:{
            default:{
                page: 1,
                size: 10,
                total: 1
            }
        }
    },
  methods: {
    handleSizeChange(val) {
        this.$emit("setPageSize",val);
    },
    handleCurrentChange(val) {
        this.$emit("jumpPage",val);
    }
  },
  data() {
      console.log("fuck you "+this.dataInfo)
    return {};
  }
};
</script>




