<template>
<div class="container-form">
    
    <el-form ref="form" :rules="rules"  :model="form" label-width="88px">
      <el-form-item label="id:" prop="userIds" >
        <el-input v-model="form.userIds" type="textarea" :autosize="{ minRows: 4, maxRows: 6 }" placeholder='","' ></el-input>
      </el-form-item>
      <el-form-item label=":" class="item-togger is-required" >
          <el-radio-group v-model="isAdd" size="small" >
            <el-radio-button label=1 >( + )</el-radio-button>
            <el-radio-button label=-1 >( - )</el-radio-button>
          </el-radio-group>
      </el-form-item>
      <el-form-item label="" prop="adjustScore">
        <el-input v-model="form.adjustScore" type="input"></el-input>
      </el-form-item>
      <el-form-item label=":">
        <el-input v-model="form.memo" type="textarea" :autosize="{ minRows: 4, maxRows: 6 }"></el-input>
      </el-form-item>
      <el-form-item label=":" class="item-togger" is-required>
          <el-radio-group v-model="isForever" size="small">
            <el-radio-button label=1 ></el-radio-button>
            <el-radio-button label=0 ></el-radio-button>
          </el-radio-group>
      </el-form-item>
       <el-form-item v-if="isForever==0" label="" prop="youxiaoqi">
              <el-date-picker
                v-model="form.youxiaoqi"
                type="datetimerange"
                range-separator=""
                value-format="yyyy-MM-dd HH:mm:ss"              
                start-placeholder=""
                end-placeholder="" >
              </el-date-picker>
      </el-form-item>
      <el-form-item class="item-space">
        <el-button :loading="loading" type="primary" @click="submit('form')"></el-button>
      </el-form-item>
    </el-form>
   <el-dialog
  title=""
  :visible.sync="dialogVisible"
  width="30%"
  >
        <el-form :model="form" label-width="88px">
            <el-form-item label="id:" class="huanhang"  >
              {{form.userIds}}
            </el-form-item>
            <el-form-item label=":">
              {{isAdd*form.adjustScore}}
            </el-form-item>
            <el-form-item label=":">
              {{form.memo}}
            </el-form-item>
            <el-form-item class="item-space">
              <el-button @click="dialogVisible = false"> </el-button>
              <el-button :loading="loading" type="primary" @click="sendData"> </el-button>
            </el-form-item>
        </el-form>
      </el-dialog>
</div>
</template>

<script>
import Vue from "vue";
import form from "../template/form/form.vue";

import { fetch } from "utils/fetch";
import cloneDeep from "lodash/cloneDeep";
import isArray from "lodash/isArray";

let getYMD = (time=new Date())=>{
    console.log(time.getFullYear()+"-"+(Number(time.getMonth())+1)+"-"+time.getDate())
    return time.getFullYear()+"-"+formateTime((Number(time.getMonth())+1))+"-"+formateTime(time.getDate())+" "+formateTime(time.getHours())+":"+formateTime(time.getMinutes())+":"+formateTime(time.getSeconds());
}
let formateTime = (value)=>{
    return value<10?("0"+value):value
}

//
let getData = async query => {
  let formateParam = query;
  let result = false;
  console.log(":");
  console.log(formateParam);
  // debugger
  try {
    result = await fetch({
      url: "/user/adjust.do?"+toExcString(query),
      method: "post",
      data: formateParam
    });
  } catch (err) {
    result = false;
  }
  return result;
};

const toExcString = function (array, type = {
    ":": "=",
    ",": "&"
}) {

    let result = "";
    for (var temp in array) {
        result += temp + '=' + encodeURI(array[temp]) + "&"
    }
    return result.substring(-1, result.length - 1);
}

export default {
  data() {
    let data = {
      form: { youxiaoqi: [] },
      rules: {
        userIds: [{ required: true, message: "id", trigger: "blur" }],
        adjustScore: [
          { required: true, message: "", trigger: "blur" },
          { message: "", pattern: /^[0-9.]+$/, trigger: "change" },
          { message: "", pattern:  /^\d+(\.\d{2})?$/, trigger: "blur" }
        ],
        youxiaoqi: [
          { required: true, type: "array", message: "", trigger: "blur" }
        ]
      },
      isAdd: 1,
      isForever: 1
    };
    let baseInfo = {
      loading: false,
      dialogVisible: false
    };
    let info = Object.assign(baseInfo, data);
    return Object.assign(baseInfo, data);
  },
  methods: {
    submit: async function(formName) {
      let k = {};
      let pro = new Promise((rev, reb) => {
        k.rev = rev;
        k.reb = reb;
      });
      this.$refs[formName].validate(valid => {
        if (valid) {
          k.rev(true);
        } else {
          console.log("error submit!!");
          k.rev(false);
        }
      });

      let info = await pro;
      if (!info) {
        return;
        //  console.log(info)
      }
      this.dialogVisible = true;
    },
    resetForm(formName) {
      this.$refs[formName].resetFields();
    },
    async sendData() {
      this.dialogVisible = false;
      let form = Object.assign({}, this.$data.form);
          form.adjustScore = Number(this.isAdd)*Number(form.adjustScore)
      if (this.isForever == 0) {
        form.validStartTime = getYMD(form.youxiaoqi[0]);
        form.validEndTime = getYMD(form.youxiaoqi[1]);
      }
      delete form.youxiaoqi;

      this.loading = true;
      let data = await getData(form);
      this.loading = false;
      if (data && data.success === true) {
          this.$alert('', "");
          this.$router.push('/point/list');
      }else{
        this.$alert((result&&result.data)||"GG", "");
      }
      
    }
  },
  components: {
    "form-form": form
  }
};
</script>

<style lang="scss">
.container-form {
  display: flex;
  // flex-direction: column;
  padding: 10px 20px 5px;
  background-color: #fff;
  form {
    min-width: 38%;
  }
}
.container-list-pagination {
  display: flex;
  justify-content: flex-end;
  padding: 10px 0px 5px;
}
.item-togger {
  margin-bottom: 10px;
}
.item-space {
  margin-top: 20px;
}
.huanhang{
  word-wrap: break-word; 
word-break: normal; 
}
</style>




