<template>
    <div class="containers-list-header">
        <div class="containers-list-search">
            <el-form :inline="true" :model="formInline" class="demo-form-inline">
                <el-form-item v-for="(one,key) in search" :key="key" :label='one.label'>
                    <el-input v-if="one.type=='input'" v-model="formInline[key]" :placeholder="one.placeholder" ></el-input>
                    <el-select v-else-if="search[key].type=='select'"  v-model="formInline[key]" :placeholder="search[key].placeholder" >
                        <el-option v-for="(item,index) in one.data" :key="item.key" :label="item.value" :value="item.key"></el-option>
                    </el-select>
                    <el-date-picker v-else-if="search[key].type=='daterange'"  v-model="formInline[key]" type="daterange" :format="search[key].format" align="right" unlink-panels range-separator="" start-placeholder="" end-placeholder="" :picker-options="pickerOptions2">
                    </el-date-picker>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" @click="onSubmit" :loading="loading" ></el-button>
                </el-form-item>
            </el-form>
            <div class="containers-list-todo">
                <el-button type="text" ></el-button>
            </div>
        </div>
        <div class="containers-list-table-todo"></div>
    </div>
</template>

<script>
let getYMD = (time=new Date())=>{
    console.log(time.getFullYear()+"-"+(Number(time.getMonth())+1)+"-"+time.getDate())
    return time.getFullYear()+"-"+(Number(time.getMonth())+1)+"-"+time.getDate();

}
let week = (() => {
  const end = new Date();
  const start = new Date();
  start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
 return [getYMD(start),getYMD(end)]
})();

let defaultFormInline = {
    userId: "",
    busType: "",
    busId: "",
    operator: "",
    scoreFlag: "",
    adjustTimes: week,
    validTimes: week,
    status: ""
};
      
export default {
  name: "header",
  props: {
    search:{
        required:true
    },
    loading:{
        default:false,
        type:Boolean
    },
    pickerOptions2: {
      default: () => {
        return {
          shortcuts: [
            {
              text: "",
              onClick(picker) {
                const end = new Date();
                const start = new Date();
                start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
                picker.$emit("pick", [start, end]);
              }
            },
            {
              text: "",
              onClick(picker) {
                const end = new Date();
                const start = new Date();
                start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
                picker.$emit("pick", [start, end]);
              }
            },
            {
              text: "",
              onClick(picker) {
                const end = new Date();
                const start = new Date();
                start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
                picker.$emit("pick", [start, end]);
              }
            }
          ]
        };
      },
      required: false
    }
  },
  methods: {
    onSubmit() {
      console.log("sbmit");
      this.$emit("submit",this.$data.formInline)
    }
  },
  data() {
    let {search} = this,formDefaultData={};
    
    for(let key in search){
        if(search[key].hasOwnProperty("value")){
            formDefaultData[key] = search[key].value;
        }
    }
    let formInline = Object.assign({},defaultFormInline,formDefaultData)
    
    return {
        formInline
    };
  }
};
</script>


<style lang="scss">
.containers-list-header {
  display: flex;
  flex-wrap: nowrap;
  justify-content: space-between;
  padding: 0.75rem 0 0 0;
}

.containers-list-search {
  display: flex;
  flex-wrap: nowrap;
  flex-direction: row;
  padding: 10px 0 0 0;
  align-items: flex-start;
  form {
    padding: 0 10px;
  }
}
.containers-list-todo {
  display: flex;
  flex-shrink: 0;
  > * {
    margin-bottom: 10px;
    margin-right: 10px;
  }
}
.containers-list-table-todo {
  display: flex;
  flex-shrink: 0;
  min-width: 12rem;
  max-width: 20%;
  padding: 5px 0 10px 0;
  > * {
    margin-right: 10px;
  }
}
.search-one {
  display: flex;
  > span {
    display: flex;
    flex-shrink: 0;
    flex-wrap: nowrap;
    // justify-content: center;
    // align-items: center;
    padding: 0 5px;
    height: 28px;
    line-height: 28px;
  }
}
.containers-list-footer {
  padding: 10px 0;
  display: flex;
  justify-content: flex-end;
}
</style>




