<template>
  <div class="container-quanzhong">
      <ul v-loading="left.loading">
          <li v-for="(item,index) in left.data" :key="index"  @click="setName(item.listName)" :class="[item.listName==name?'li-select':'']" >
              {{item.listName+(item.listName==checkName?"()":"")}}
          </li>
      </ul>
      <div>
          <router-link to="/radio/add"> <el-button type="text" ></el-button></router-link>
          <el-table :data="right.data" v-loading="right.loading">
              <el-table-column type="index" label="" width="80"></el-table-column>
              <el-table-column prop="startScore" label="" width="100"></el-table-column>
              <el-table-column prop="endScore" label="" width="100"></el-table-column>
              <el-table-column label="" width="180">
                <template scope="ff">
                  <span style="margin-left: 10px">{{ ff.row.ratio*100+"%" }}</span>
                </template>
              </el-table-column>
              <!-- <el-table-column  label="" width="100">
                  <template slot-scope="scope">
                    <span >{{scop.row.listName}}</span>
                  </template>
              </el-table-column> -->
              <el-table-column prop="createTime" label="" width="220"></el-table-column>
              <el-table-column prop="updateTime" label="" width="220"></el-table-column>
              <el-table-column prop="operator" label="" width="100">
                  <template scope="ff">
                  <span style="margin-left: 10px">{{ ff.row.status===false?"":""}}</span>
                </template>
              </el-table-column>
              <el-table-column prop="operator" label="" width="100"></el-table-column>
          </el-table>
          <div class="quanzhong-todos">
              <el-button :loading="loading" @click="dialogVisible = true" type="primary" v-show="name!=checkName"></el-button>
          </div>

          <el-dialog
            title=""
            :visible.sync="dialogVisible"
            size="tiny">
            <span>({{name}})({{checkName}})</span>
            <span slot="footer" class="dialog-footer">
              <el-button @click="dialogVisible = false"> </el-button>
              <el-button type="primary" @click="setCheckName"> </el-button>
            </span>
          </el-dialog>
          
      </div>
  </div>
</template>

<script>
import { fetch } from "utils/fetch";

let getData = async (url, method = "get", data = {}) => {
  let result = false;
  try {
    result = await fetch({
      url,
      method,
      data
    });
  } catch (err) {
    result = false;
  }
  return result;
};

// const getLeftData = async () => {
//   let data = getData("/confratio/getRatios");
//   this.LeftData = data.data;

//   let name = "";
//   data.data.each((one, indx) => {
//     //status
//     if (one.status === true) {
//       name = one.listName;
//     }
//     return false;
//   });
//   this.name = name;
//   this.checkName = name;
//   this.getRightData(name);
// };

export default {
  data() {
    return {
      left: { data: [], loading: true },
      right: { data: [], loading: true },
      name: false,
      checkName: "",
      loading:false,
      dialogVisible:false
    };
  },
  beforeCreate() {
    // this.getLeftData = ;
  },
  async mounted() {
    let data = await getData("/confratio/getDis.do");
    
    this.left.loading = false;
    
    this.left.data = (data&&data.data)||[];
    let name = "";
    this.left.data.map((one, indx) => {
        
        if (one.status === false) {
            name = one.listName;
        }
        return false;
    });
    this.name = this.checkName = name;
    this.right.loading = true;
    
    let rightData = await this.getRightData(name);
    
    this.right.data = (rightData&&rightData.data)||[];
    this.right.loading = false;
  },
  methods: {
    setName: async  function(name){
        // debugger
        this.name = name;
        this.right.loading = true;
        let rightData = await this.getRightData(name);
        this.right.data = rightData.data;
        this.right.loading = false;
        //   this.checkName
    },
    setCheckName :async function() {
        let name = this.name;
        this.dialogVisible = false;
        if(name==this.checkName){
            return;
        }
        this.loading = true;
        let result = await getData('/confratio/upConfRatio.do',"post",{listName:name})
        this.loading = false;
        if( result&&result.success){
            this.checkName  = name;
        }
        
    },
    getRightData:async function(name){
        return await getData(
            "/confratio/getListBylistName.do?listName=" + name
        );
    },
    
  }
};
</script>
<style lang="scss">
.container-quanzhong {
    display:flex;
    flex-direction: row;
    padding: 20px;
    >ul{
        list-style-type:none;
        margin-right: 20px;
        margin-top: 0;
         
    }
    .li-select{
        background-color: #20a0ff;
        color: white;
    }
    li{
        width: 100px;
        cursor: pointer;
        // height: 50px;
        padding: 20px 10px;
        text-align: center;
        border-bottom: 1px solid  #dfe6ec;
         border-left: 1px solid  #dfe6ec;
         border-right: 1px solid  #dfe6ec;
       &:first-of-type{
            border-top: 1px solid  #dfe6ec;
        }
    }
    
}
.quanzhong-todos{
    margin-top: 20px;
  // padding-left: 20px;
}
</style>



