<template>
  <div class="credit-container">
    <div>
      <router-link to="behaviorAdd">
        <el-button type="primary"></el-button>
      </router-link>
    </div>
    <div>
      <el-table :data="list" border style="width: 100%">
        <el-table-column prop="busName" label="" width="180"></el-table-column>
        <el-table-column label="" width="180" >
          <template  scope="scope">
            <img v-bind:src="scope.row.imageUrl" class="thumbnail"/>
          </template>
        </el-table-column>
        <el-table-column   prop="title"   label="" width="100"></el-table-column>
        <el-table-column   prop="subTitle"   label="" width="100"></el-table-column>
        <el-table-column   prop="classType"   label="" width="180">
           <template scope="scope">
            <span v-if="scope.row.classType === 2">native</span>
            <span v-if="scope.row.classType === 1">h5</span>
            <span v-if="scope.row.classType === 0"></span>
          </template>
        </el-table-column>
<!--          <el-table-column label="" width="180">

         <template scope="scope">
            <span v-if="scope.row.classType === 2">{{scope.row.className}}</span>
            <span v-if="scope.row.classType === 2">{{scope.row.linkUrl}}</span>
            <span v-if="scope.row.classType === 1">{{scope.row.linkUrl}}</span>
            <span v-if="scope.row.classType === 0"></span>
          </template>
        </el-table-column> -->

        <el-table-column   prop="createTime"   label="" width="180"></el-table-column>
        <el-table-column   prop="updateTime"   label="" width="180"></el-table-column>
        <el-table-column label="" width="100">
          <template scope="scope">
            <span v-if="scope.row.status === 0"></span>
            <span v-if="scope.row.status === 1"></span>
          </template>
        </el-table-column>
        <el-table-column   prop="operator"   label="" width="180"></el-table-column>

        <el-table-column label="" align="center" width="160">
          <template scope="scope">
            
            <el-button v-if="scope.row.status == 1" size="small" type="danger" @click="editItem(scope.row)">
            </el-button>
            <el-button  v-if="scope.row.status == 1" size="small" type="primary" @click="commitUp(scope.row.id,0)"></el-button>
            <el-button  v-if="scope.row.status == 0" size="small" type="primary"@click="commitDown(scope.row.id,1)"></el-button>
          </template>
        </el-table-column>
      </el-table>

      <div v-show="!listLoading" class="pagination-container">
        <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="listQuery.page" :page-sizes="[5,10,20,30,50]"
          :page-size="listQuery.rows" layout="total, sizes, prev, pager, next, jumper" :total="total">
        </el-pagination>
      </div>

      <el-dialog title="" class="eidt_dialog" v-model="editorDialogVisable">
        <div class="form-container">
          <el-form ref="form" :model="form" :rules="rules" label-width="110px" class="form">
            <el-form-item label="" prop="busName">
              <el-input v-model="form.busName"></el-input>
            </el-form-item>
            <el-form-item label="" prop="title">
              <el-input v-model="form.title"></el-input>
            </el-form-item>
            <el-form-item label="" prop="subTitle">
              <el-input v-model="form.subTitle"></el-input>
            </el-form-item>
            <el-form-item label="" required>
              <!-- <template  scope="scope">
                <img v-bind:src="scope.row.imageUrl" class="thumbnail"/>
              </template> -->
              <img v-bind:src="form.imageUrl" class="thumbnail"/>
              <div id="container">
                <el-button size="small" type="primary" id="pickfiles"></el-button>
              </div>
              
              
            </el-form-item>
            <el-form-item label="" class="url_links" required>
              <template>
                <el-radio-group v-model="radio2" class="link_list" @change="radioChange">
                  <el-radio :label="0" ></el-radio>
                  <el-radio :label="1" >h5</el-radio>
                  <el-radio :label="2" >native</el-radio>
                </el-radio-group>
              </template>

              <div class="links_group">
                <div v-show="!native_group" class="native_group">
                  <span class="native_group_item">
                    <i>IOS</i>
                    <el-input v-model="classNameIos"></el-input>
                  </span>
                  <span class="native_group_item">
                    <i>Android</i>
                    <el-input v-model="classNameAndrow  "></el-input>
                  </span>
                  <span class="native_group_item">
                    <i>URL</i>
                    <!-- <el-input v-model="form.linkUrl  "></el-input> -->
                    <el-input v-model="h5_LinkUrl"></el-input>
                  </span>
                </div>
                <span class="link_h5" v-show="!h5_group">
                  <el-input v-model="form.linkUrl  "></el-input>
                  <span class="txt_desc">url</span>
                </span>
              </div>

            </el-form-item>
            <el-form-item>
              <el-button type="primary" @click="commitEidt"></el-button>
            </el-form-item>
          </el-form>
        </div>
      </el-dialog>
    </div>
  </div>
</template>

<script>
import { list, save, update,querybyid} from 'api/behavior';
import { objectMerge} from 'utils';
import { uploadQiNiu} from 'utils/uploadQiNiu';


export default {
  name: 'behavior',
  data() {
    return {
      radio2:0,
      native_group:true,
      h5_group:true,
      h5_LinkUrl:'',
      classNameIos:'',
      classNameAndrow:'',
      list:null,
      total: null,
      listLoading: true,
      uploadSign:'true',
      sourceLink:'',
      listQuery: {
        page: 1,
        rows: 20,
        keyword: ''
      },
      form:{
        id:'',
        busName: '',
        title: '',
        subTitle:'',
        imageUrl:'',
        linkUrl:'',
        className:'',
        classType:0
      },
      temp:{
        id:'',
        name: '',
        title: '',
        subTitle:'',
        links:''
      },
      editorDialogVisable: false,
      fileList2:[],
      rules: {
        busName: [
          { required: true, message: '', trigger: 'blur' },
          { min: 0, max: 100, message: ' 3  5 ', trigger: 'blur' }
        ],
        title: [
           { required: true, message: '', trigger: 'blur' },
           { min: 0, max: 100, message: ' 3  5 ', trigger: 'blur' }
        ],
        subTitle: [
          { required: true, message: '', trigger: 'blur' },
           { min: 0, max: 100, message: ' 3  5 ', trigger: 'blur' }
        ]
        
      }//
    }
  },
  created() {
    this.getList();
  },
  mounted(){
    
  },
  methods:{
    radioChange(val){
      if(val == 0){
        this.h5_group = true;
        this.native_group = true;
        this.form.classType = val;
      }
      if(val == 1){
        this.h5_group = false;
        this.native_group = true;
        this.form.classType = val;

        // this.classNameIos='';
        // this.classNameAndrow='';
        // this.h5_LinkUrl='';

      }
      if(val == 2){
        this.native_group = false;
        this.h5_group = true;
        this.form.classType = val;

        // this.form.linkUrl = '';
      }
    },
    getList() {
      list(this.listQuery).then(response => {
        if (response.success) {

          this.total = response.totalCount;
          this.list = response.data;
          this.listLoading = false;

        } else {
          this.$notify({
            title: '',
            message: response.message,
            type: 'error',
            duration: 2000
          })
        }
      })
    },
    editItem(row){

      this.editorDialogVisable = true;

      querybyid(row.id).then(response => {
        if (response.success) {
          objectMerge(this.form, response.data);

          if(this.form.classType == '2'){
            this.h5_LinkUrl = response.data.linkUrl;
            this.form.linkUrl = '';
          }
          if(this.form.classType == '1'){
            // this.h5_LinkUrl = response.data.linkUrl;
          }

          this.radio2 =response.data.classType;
          this.classNameIos = response.data.className.split(',')[0];
          this.classNameAndrow = response.data.className.split(',')[1];
        } else {
          this.$notify({
            title: '',
            message: response.message,
            type: 'error',
            duration: 2000
          });
        }
      })   

      if(this.uploadSign){
        this.uploadSign = false;

        var _this = this;
        setTimeout(function(){
          uploadQiNiu(_this)
        },500);
      }
    },
    commitUp(id,status){

      update(id,status).then(response => {
        if (response.success) {
          this.$notify({
            title: '',
            message: '',
            type: 'success',
            duration: 2000
          });
          // this.editorDialogVisable = false
          this.getList()
        } else {
          this.$notify({
            title: '',
            message: response.message,
            type: 'error',
            duration: 2000
          });
        }
      })
    },
    commitDown(id,status){

      update(id,status).then(response => {
        if (response.success) {
          this.$notify({
            title: '',
            message: '',
            type: 'success',
            duration: 2000
          });
          // this.editorDialogVisable = false
          this.getList()
        } else {
          this.$notify({
            title: '',
            message: response.message,
            type: 'error',
            duration: 2000
          });
        }
      })
    },
    commitEidt(){

      if(this.radio2 == '2'){
        this.form.linkUrl = this.h5_LinkUrl;
        // console.log((this.classNameIos == '' || this.classNameAndrow == '' || this.h5_LinkUrl == ''));

        if(this.classNameIos == '' ){
          this.$notify({
            title: '',
            message: '',
            type: 'error',
            duration: 2000
          });
          return false;
        }

        if(this.classNameAndrow == '' ){
          this.$notify({
            title: '',
            message: '',
            type: 'error',
            duration: 2000
          });
          return false;
        }

        // if(this.h5_LinkUrl == '' ){
        //   this.$notify({
        //     title: '',
        //     message: '',
        //     type: 'error',
        //     duration: 2000
        //   });
        //   return false;
        // }
      }

      if(this.radio2 == '1'){

         if(this.form.linkUrl == '' ){
          this.$notify({
            title: '',
            message: 'h5',
            type: 'error',
            duration: 2000
          });
          return false;
        }
      }

      this.form.className= this.classNameIos+','+this.classNameAndrow;

      if(this.radio2 == 0 ){
        this.form.className='';
        this.form.linkUrl='';
         this.form.classType=0;
      }
      if(this.radio2 == 1 ){
        this.form.className='';
        this.form.classType=1;
      }
      if(this.radio2 == 2 ){
        this.form.classType=2;
      }

      if(this.form.busName == ""){
          this.$notify({
            title: '',
            message: '',
            type: 'error',
            duration: 2000
          });
          return false;
        }
        if(this.form.title == ""){
          this.$notify({
            title: '',
            message: '',
            type: 'error',
            duration: 2000
          });
          return false;
        }
        if(this.form.subTitle == ""){
          this.$notify({
            title: '',
            message: '',
            type: 'error',
            duration: 2000
          });
          return false;
        }
        if(this.form.imageUrl == ""){
          this.$notify({
            title: '',
            message: '',
            type: 'error',
            duration: 2000
          });
          return false;
        }

        if(this.form.classType === ''){
          this.$notify({
            title: '',
            message: '',
            type: 'error',
            duration: 2000
          });
          return false;
        }

      save(this.form).then(response => {
        if (response.success) {
          this.$notify({
            title: '',
            message: '',
            type: 'success',
            duration: 2000
          });
          this.editorDialogVisable = false; 
          this.h5_LinkUrl='';  
          this.getList()            
        } else {
          this.$notify({
            title: '',
            message: response.message,
            type: 'error',
            duration: 2000
          });
        }
      })
    },
    handleSizeChange(val) {
      this.listQuery.rows = val;
      this.getList();
    },
    handleCurrentChange(val) {
      this.listQuery.page = val;
      this.getList();
    }
  }
}
</script>

<style lang="scss">
  @import "src/styles/mixin.scss";

  .credit-container {
    padding: 20px;
  }
  .credit-container .thumbnail{
    max-height:60px;
  }
  .native_group_item{
    display:block;
    padding:8px 0;
  }
  .eidt_dialog {}
  .eidt_dialog > div{
    width:60%;
  }
  .eidt_dialog > div .el-input{
    width:80%;
  }
  .eidt_dialog .native_group i{
    width:60px;
    padding-right:6px;
    font-style:normal;
    display:inline-block;
  }
  .txt_desc{
    display:block;
  }
</style>



