import {
  fetch
} from 'utils/fetch';

export function list(query) {
  return fetch({
    // url: 'http://creditba.qbao.com/ratioScore/list.do?page=' + query.page + '&rows=' + query.rows,
    url: '/ratioScore/list.do?page=' + query.page + '&rows=' + query.rows,
    method: 'get',
    data: ''
  });
}

export function save(list) {
  return fetch({
    // url: 'http://creditba.qbao.com/ratioScore/save.do',
    url: '/ratioScore/save.do',
    method: 'post',
    data: list,
    headers: {
      'Content-Type': 'application/json;charset=UTF-8'
    },
  });

}

export function update(id, status) {
  return fetch({
    // url: 'http://creditba.qbao.com/ratioScore/update.do',
    url: '/ratioScore/update.do',
    method: 'post',
    data: {
      id: id,
      status: status
    },
    headers: {
      'Content-Type': 'application/json;charset=UTF-8'
    },
  });
}

export function querybyid(id) {
  return fetch({
    // url: 'http://creditba.qbao.com/ratioScore/querybyid.do?id=' + id,
    url: '/ratioScore/querybyid.do?id=' + id,
    method: 'get',
    data: '',
    headers: {
      'Content-Type': 'application/json;charset=UTF-8'
    },
  });
}

export function infoUp(id) {
  return fetch({
    // url: 'http://creditba.qbao.com/user/detail.do?userId=' + id,
    url: '/user/detail.do?userId=' + id,
    method: 'get',
    data: ''
  });
}

export function infoDown(userId, query) {
  return fetch({
    url: '/user/detaiList.do?userId=' + userId + '&page=' + query.page + '&size=' + query.rows,
    // url: 'http://creditba.qbao.com/user/detaiList.do?userId=' + userId + '&page=' + query.page + '&size=' + query.rows,
    method: 'get',
    data: '',
    headers: {
      'Content-Type': 'application/json;charset=UTF-8'
    },
  });
}


