<template>
    <div class="user-container">
        <el-form :inline="true" :model="param" class="demo-form-inline">
            <el-row type="flex" class="row-bg" justify="left">
                <el-form-item label="id">
                    <el-col :span="16">
                        <el-input placeholder="ID" v-model="param.userId"></el-input>
                    </el-col>
                </el-form-item>

                <el-form-item label="">
                    <el-col :span="8">
                        <el-input placeholder="" v-model="param.ratiosStart"></el-input>
                    </el-col>
                    <el-col class="line" :span="2">%</el-col>
                    <el-col class="line" :span="2"></el-col>
                    <el-col :span="8">
                        <el-input placeholder="" v-model="param.ratiosEnd"></el-input>
                    </el-col>
                    <el-col class="line" :span="2">%</el-col>
                </el-form-item>

                <el-form-item label="">
                    <el-col :span="8">
                        <el-input placeholder="" v-model="param.totalScoresStart"></el-input>
                    </el-col>

                    <el-col class="line" :span="2"></el-col>

                    <el-col :span="8">
                        <el-input placeholder="" v-model="param.totalScoresEnd"></el-input>
                    </el-col>
                </el-form-item>

            </el-row>

            <el-row type="flex" class="row-bg" justify="left">
                <el-form-item label="">
                    <el-col :span="8">
                        <el-input placeholder="" v-model="param.creditScoresStart"></el-input>
                    </el-col>
                    <el-col class="line" :span="2"></el-col>
                    <el-col :span="8">
                        <el-input placeholder="" v-model="param.creditScoresEnd"></el-input>
                    </el-col>
                </el-form-item>

                <el-form-item label="">
                    <el-col :span="8">
                        <el-input placeholder="" v-model="param.addScoresStart"></el-input>
                    </el-col>
                    <el-col class="line" :span="2"></el-col>
                    <el-col :span="8">
                        <el-input placeholder="" v-model="param.addScoresEnd"></el-input>
                    </el-col>
                </el-form-item>

                <el-form-item label="">
                    <el-col :span="8">
                        <el-input placeholder="" v-model="param.adjustScoresStart"></el-input>
                    </el-col>
                    <el-col class="line" :span="2"></el-col>
                    <el-col :span="8">
                        <el-input placeholder="" v-model="param.adjustScoresEnd"></el-input>
                    </el-col>
                </el-form-item>


            </el-row>

            <el-row type="flex" class="row-bg" justify="left">

                <el-form-item label="">
                    <el-date-picker
                            v-model="addTime"
                            type="datetimerange"
                            range-separator=""
                            start-placeholder=""
                            end-placeholder="">
                    </el-date-picker>
                </el-form-item>

                <el-form-item label="">
                    <el-date-picker
                            v-model="time"
                            type="datetimerange"
                            range-separator=""
                            start-placeholder=""
                            end-placeholder="">
                    </el-date-picker>
                </el-form-item>

            </el-row>

            <div>
                <el-button class="btn-small" type="primary" @click="getUserList"></el-button>
            </div>

            <div class="userinfo-detail">
                <router-link to="/point/list">
                    <el-button class="btn-small" type="primary"></el-button>
                </router-link>
            </div>

        </el-form>


        <div class="list-container">
            <el-table :data="list" border style="width: 100%">
                <el-table-column prop="userId" label="id"></el-table-column>
                <el-table-column prop="ratio" label=""></el-table-column>
                <el-table-column prop="totalScore" label=""></el-table-column>
                <el-table-column prop="creditScore" label=""></el-table-column>
                <el-table-column prop="addScore" label=""></el-table-column>
                <el-table-column prop="lastAddTime" label=""></el-table-column>
                <el-table-column prop="adjustScore" label=""></el-table-column>
                <el-table-column prop="lastAdjustTime" label=""></el-table-column>
                <el-table-column label="" align="center" width="150">
                    <template scope="scope">
                        <router-link :to="{ path: '/userInfoDetail', query: { userId: scope.row.userId }}">
                            <el-button type="primary"></el-button>
                        </router-link>
                    </template>
                </el-table-column>

            </el-table>
        </div>

        <div v-show="!listLoading" class="pagination-container">
            <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange"
                           :current-page="listQuery.page" :page-sizes="[5,10,20,30,50]"
                           :page-size="listQuery.rows" layout="total, sizes, prev, pager, next, jumper" :total="total">
            </el-pagination>
        </div>

    </div>
</template>

<script>
    import {list} from 'api/userinfo.js';
    import { formatDate } from 'utils';

    export default {
        name: 'userinfo',
        data() {
            return {
                // list: null,
                list:[],
                param: {
                    userId: '',
                    ratiosStart: '',
                    ratiosEnd: '',
                    creditScoresStart: '',
                    creditScoresEnd: '',
                    totalScoresStart: '',
                    totalScoresEnd: '',
                    addScoresStart: '',
                    addScoresEnd: '',
                    adjustScoresStart: '',
                    adjustScoresEnd: '',
                },
                listQuery: {
                    page: 1,
                    rows: 10,
                    keyword: ''
                },
                total: 0,
                newParam: {
                    page: 1,
                    size: 10
                },
                time: '',
                listLoading: true,
                addTime:''
            }
        },

        created() {
            this.getList();
        },

        methods: {

            getList() {

                this.newParam.page = this.listQuery.page;
                this.newParam.size = this.listQuery.rows;

                list(this.newParam).then(response => {
                        if (response.success) {
                        this.list = response.data.items;
                        this.listLoading = false;
                        this.total = response.data.total;
                    }
                })
            },
            handleSizeChange(val) {
                this.listQuery.rows = val;
                this.getList();
            },
            handleCurrentChange(val) {
                this.listQuery.page = val;
                this.getList();
            },

            getUserList() {
                var dataParam = this.param;

                if (dataParam.userId == "" || dataParam.userId==undefined) {
                    delete this.newParam.userId;
                }else{
                    this.newParam.userId = dataParam.userId;
                }

                //
                if(this.time[0] == undefined || this.time[1] == undefined ){
                    delete this.newParam.adjustTimes;

                }else if (this.time.length > 0) {
                    var timeStart = formatDate(new Date(this.time[0]), "yyyy-MM-dd hh:mm:ss");
                    var timeEnd = formatDate(new Date(this.time[1]), "yyyy-MM-dd hh:mm:ss");
                    this.newParam.adjustTimes = timeStart + ',' + timeEnd;
                    this.newParam.adjustTimes = encodeURIComponent(this.newParam.adjustTimes);
                }

                if(this.addTime[0] == undefined || this.addTime[1] == undefined){
                    delete this.newParam.addTimes;
                }else if(this.addTime.length > 0){
                    var addTimeStart = formatDate(new Date(this.addTime[0]), "yyyy-MM-dd hh:mm:ss");
                    var addTimeEnd = formatDate(new Date(this.addTime[1]), "yyyy-MM-dd hh:mm:ss");
                    this.newParam.addTimes = addTimeStart +','+addTimeEnd;
                    this.newParam.addTimes = encodeURIComponent(this.newParam.addTimes);
                }

                if(dataParam.ratiosStart == ""
                    || dataParam.ratiosStart == undefined
                    || dataParam.ratiosEnd == ""
                    ||  dataParam.ratiosEnd == undefined){
                    delete this.newParam.ratios;

                }else{
                    this.newParam.ratios = dataParam.ratiosStart / 100 + ',' + dataParam.ratiosEnd / 100;
                }


                if(dataParam.creditScoresStart == ""
                    || dataParam.creditScoresStart == undefined
                    || dataParam.creditScoresEnd == ""
                    || dataParam.creditScoresEnd == undefined){
                    delete this.newParam.creditScores;
                }else{
                    this.newParam.creditScores = dataParam.creditScoresStart + ',' + dataParam.creditScoresEnd;
                }

                if(dataParam.totalScoresStart == ""
                    || dataParam.totalScoresStart == undefined
                    || dataParam.totalScoresEnd ==""
                    || dataParam.totalScoresEnd == undefined){
                    delete this.newParam.totalScores;
                }else{
                    this.newParam.totalScores = dataParam.totalScoresStart + ',' + dataParam.totalScoresEnd;
                }

                if(dataParam.addScoresStart == ""
                    || dataParam.addScoresStart == undefined
                    || dataParam.addScoresEnd == ""
                    || dataParam.addScoresEnd == undefined){
                    delete this.newParam.addScores;
                }else{
                    this.newParam.addScores = dataParam.addScoresStart + ',' + dataParam.addScoresEnd;
                }

                if(dataParam.adjustScoresStart == ""
                    || dataParam.adjustScoresStart == undefined
                    || dataParam.adjustScoresEnd ==""
                    || dataParam.adjustScoresEnd == undefined){
                    delete this.newParam.adjustScores;
                }else{
                    this.newParam.adjustScores = dataParam.adjustScoresStart + ',' + dataParam.adjustScoresEnd;
                }
                this.getList();
            }
        }
    }
</script>

<style rel="stylesheet/scss" lang="scss">
    @import "src/styles/mixin.scss";

    .user-container {
    @include relative;
        height: 100vh;
        background-color: #ffffff;
        padding: 30px 30px;

    .userinfo-detail {
        margin-top: 20px;
        margin-bottom: 20px;
    }
    }
</style>



