// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue';
import App from './App';
import router from './router';
import store from './store';
import ElementUI from 'element-ui';
import 'element-ui/lib/theme-default/index.css';
import NProgress from 'nprogress';
import 'nprogress/nprogress.css';
import 'normalize.css/normalize.css';
import 'styles/index.scss';
import * as filters from './filters';
import Multiselect from 'vue-multiselect';
import Icon from 'components/Icon';
import 'vue-multiselect/dist/vue-multiselect.min.css';
import vueWaves from './directive/waves';
import vueSticky from './directive/sticky';
import errLog from 'store/errLog';
// import './mock/index.js';  // api!!
// import './styles/mixin.scss';
import permission from 'store/permission';

// register globally
Vue.component('multiselect', Multiselect);
Vue.component('Icon', Icon);
Vue.use(ElementUI);
Vue.use(vueWaves);
Vue.use(vueSticky);


// register global utility filters.
Object.keys(filters).forEach(key => {
  Vue.filter(key, filters[key])
});

// permissiom judge
function hasPermission(roles, permissionRoles) {
  if (roles.indexOf('admin') >= 0) return true; // admin 
  return roles.some(role => permissionRoles.indexOf(role) >= 0)
}

// register global progress.


// const whiteList = ['/login', '/authredirect', '/reset', '/sendpwd', '/basic-credit', '/basic-update', '/business-credit', '/credit-edit', '/credit-sign', '/sign-update', '/behavior', '/behaviorAdd']; // 
const whiteList = ['/login', '/authredirect', '/reset', '/behavior', '/behaviorAdd', '/userInfoDetail', '/sendpwd', '/basic-credit', '/basic-update', '/business-credit', '/credit-edit', '/credit-sign', '/sign-update', '/userInfoDetail', '/userinfo', '/point/add','/point/list','/radio/list','/radio/add','/task/index','/task/taskAdd','/task/taskSettings','/task/taskSettingsModify']; // 

router.beforeEach((to, from, next) => {
  NProgress.start(); // Progress
  if (store.getters.token) { // token
    if (to.path === '/login') {
      next({
        path: '/'
      });
    } else {
      if (to.meta && to.meta.role) { // 
        if (store.getters.uid) { // info
          if (hasPermission(store.getters.roles, to.meta.role)) { // 
            next(); // 
          } else {
            next('/401'); // 
          }
        } else { // info
          store.dispatch('GetInfo').then(() => { // info
            permission.init({ // 
              roles: store.getters.roles,
              router: router.options.routes
            });
            if (hasPermission(store.getters.roles, to.meta.role)) { // 
              next(); // 
            } else {
              next('/401'); // 
            }
          }).catch(err => {
            console.log(err);
          });
        }
      } else { //  
        next();
      }
    }
  } else {
    if (whiteList.indexOf(to.path) !== -1) { // 
      next()
    } else {
      next('/login'); // 
      NProgress.done(); // hash hash  afterEach hack pshistory
    }
  }
});

router.afterEach(() => {
  NProgress.done(); // Progress
});

// window.onunhandledrejection = e => {
//     console.log('unhandled', e.reason, e.promise);
//     e.preventDefault()
// };

// 
if (process.env === 'production') {
  Vue.config.errorHandler = function(err, vm) {
    console.log(err, window.location.href);
    errLog.pushLog({
      err,
      url: window.location.href,
      vm
    })
  };
}

// window.onerror = function (msg, url, lineNo, columnNo, error) {
//     console.log('window')
// };
//
// console.error = (function (origin) {
//     return function (errorlog) {
//         // handler();//
//         console.log('console'+errorlog)
//         origin.call(console, errorlog);
//     }
// })(console.error);

new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app');


