<template>
    <el-table :data="tableData" stripe style="">
         <el-table-column prop="userId" label="id" width="120"></el-table-column>
         <el-table-column prop="busName" label="" width="100"></el-table-column>
         <el-table-column prop="busId" label="id" width="180"></el-table-column>
         <el-table-column prop="score" label="" width="100"></el-table-column>
         <el-table-column prop="adjustTime" label="" width="210"></el-table-column>
         <el-table-column label="" width="210">

             <template scope="ff">
                  <span style="margin-left: 10px">{{ ff.row.validStartTime?(ff.row.validStartTime+""+ff.row.validEndTime):"" }}</span>
                </template>
         </el-table-column>
         <el-table-column prop="status" label="" width="100"></el-table-column>
         <el-table-column prop="memo" label=""></el-table-column>
         <el-table-column prop="operator" label="" width="100"></el-table-column>
  </el-table>
</template>

<script>

import Vue from "vue";
export default {
    props:{
        tableData:{
             default:[]
        }
    }
}
</script>



