<template>
    <div class="containers-list-header">
        <div class="containers-list-search">
            <el-form :inline="true" :model="formInline" class="demo-form-inline">
                <el-form-item label="id">
                    <el-input v-model="formInline.userId" placeholder="id"></el-input>
                </el-form-item>
                <!-- {{selectData.busType}} -->
                <el-form-item  :label="selectData.busType" label="">
                  
                    <el-select v-model="formInline.busType" placeholder="" >
                      <el-option label="" value=""></el-option>
                        <el-option v-for="item in selectData.busType"  :key="item.key" :label="item.key" :value="item.value"></el-option>
                        <!-- <el-option label="" value="beijing"></el-option>
                        <el-option label="" value="beijing"></el-option> -->
                    </el-select>
                </el-form-item>
                <el-form-item label="id" >
                    <el-input v-model="formInline.busId" placeholder="id"></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-select v-model="formInline.operator" placeholder="">
                      <el-option label="" value=""></el-option>
                      <el-option v-for="item in selectData.operator"  :key="item.key" :label="item.key" :value="item.value"></el-option>
                        <!-- <el-option label="" value="shanghai"></el-option> -->
                    </el-select>
                </el-form-item>
                <el-form-item label="">
                    <el-select v-model="formInline.scoreFlag" placeholder="">
                        <el-option label="" value=""></el-option>
                        <el-option label="" value=1></el-option>
                        <el-option label="" value=0></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="">
                    <el-date-picker v-model="formInline.adjustTimes" :clearable="true" type="daterange" format="yyyy-MM-dd" align="right" unlink-panels range-separator="" start-placeholder="" end-placeholder="" :picker-options="pickerOptions2">
                    </el-date-picker>
                </el-form-item>
                <el-form-item label="">
                    <el-date-picker v-model="formInline.validTimes" :clearable="true" type="daterange" format="yyyy-MM-dd" align="right" unlink-panels range-separator="" start-placeholder="" end-placeholder="" :picker-options="pickerOptions2">
                    </el-date-picker>
                </el-form-item>
                <el-form-item label="">
                    <el-select v-model="formInline.status" placeholder="">
                        <el-option label="" value=0></el-option>
                        <el-option label="" value=1></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" @click="onSubmit" :loading="loading" ></el-button>
                </el-form-item>
            </el-form>
            <div class="containers-list-todo">
                <router-link to="/point/add"> <el-button type="text" ></el-button></router-link>
            </div>
        </div>
        <div class="containers-list-table-todo"></div>
    </div>
</template>

<script>
let getYMD = (time=new Date())=>{
    console.log(time.getFullYear()+"-"+(Number(time.getMonth())+1)+"-"+time.getDate())
    return time.getFullYear()+"-"+(Number(time.getMonth())+1)+"-"+time.getDate();

}
let week = (() => {
  const end = new Date();
  const start = new Date();
  start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
 return [getYMD(start),getYMD(end)]
})();

import { fetch } from "utils/fetch";

let getData = async (url, method = "get", data = {}) => {
  let result = false;
  try {
    result = await fetch({
      url,
      method,
      data
    });
  } catch (err) {
    result = false;
  }
  return result;
};


let defaultFormInline = {
    userId: "",
    busType: "",
    busId: "",
    operator: "",
    scoreFlag: "",
    adjustTimes: [],
    validTimes: [],
    status: ""
};
      
export default {
  name: "header",
  props: {
    formDefaultData: {
        required:true,

    },
    loading:{
        default:false,
        type:Boolean
    },
    pickerOptions2: {
      default: () => {
        return {
          shortcuts: [
            {
              text: "",
              onClick(picker) {
                const end = new Date();
                const start = new Date();
                start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
                picker.$emit("pick", [start, end]);
              }
            },
            {
              text: "",
              onClick(picker) {
                const end = new Date();
                const start = new Date();
                start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
                picker.$emit("pick", [start, end]);
              }
            },
            {
              text: "",
              onClick(picker) {
                const end = new Date();
                const start = new Date();
                start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
                picker.$emit("pick", [start, end]);
              }
            }
          ]
        };
      },
      required: false
    }
  },
  mounted(){
    // debugger
    this.getSelectData("busType");
    this.getSelectData("operator");

  },
  
  methods: {
    onSubmit() {
      console.log("sbmit");
      this.$emit("submit",this.$data.formInline)
    },
    async getSelectData(name){
        if(this.selectData[name]){
          return;
        }
        let data = await getData("/dict/list.do?fieldName="+name);
        // this.selectData[name] = data.data;
        this.$set(this.selectData,name,data.data)
        // debugger
        // this.loading = true;
    },
    change(value){
      console.log(value,value)
    }
    
  },

  data() {
    
    let formInline = Object.assign({},defaultFormInline,this.formDefaultData)
    return {
        formInline,
        selectData:{}
    };
  }
};
</script>


<style lang="scss">
.containers-list-header {
  display: flex;
  flex-wrap: nowrap;
  justify-content: space-between;
  padding: 0.75rem 0 0 0;
}

.containers-list-search {
  display: flex;
  flex-wrap: nowrap;
  flex-direction: row;
  padding: 10px 0 0 0;
  align-items: flex-start;
  form {
    padding: 0 10px;
  }
}
.containers-list-todo {
  display: flex;
  flex-shrink: 0;
  > * {
    margin-bottom: 10px;
    margin-right: 10px;
  }
}
.containers-list-table-todo {
  display: flex;
  flex-shrink: 0;
  min-width: 12rem;
  max-width: 20%;
  padding: 5px 0 10px 0;
  > * {
    margin-right: 10px;
  }
}
.search-one {
  display: flex;
  > span {
    display: flex;
    flex-shrink: 0;
    flex-wrap: nowrap;
    // justify-content: center;
    // align-items: center;
    padding: 0 5px;
    height: 28px;
    line-height: 28px;
  }
}
.containers-list-footer {
  padding: 10px 0;
  display: flex;
  justify-content: flex-end;
}
</style>




