import {
    fetch
} from 'utils/fetch';

export function list(newParam) {

    var params = [];
    for (var key in newParam) {
        params.push(key + '=' + newParam[key]);
    }
    return fetch({
        //url: 'http://10.172.20.41:8080/user/list.do?' + params.join('&'),
        url: '/user/list.do?' + params.join('&'),
        method: 'get',
        data: ''
    });
}



