<template>
<div class="container-list">
    <list-header :loading="loading" :formDefaultData="header.formInline" v-on:submit="submit"></list-header>
    <list-table  :tableData="table"  v-loading="loading" ></list-table>
    <list-page   :dataInfo="page" v-on:jumpPage="jumpPage" v-on:setPageSize="setPageSize" ></list-page>
</div>
</template>

<script>
import Vue from "vue";
import Header from "./header/index.vue";
import Table from "./table";
import Pageination from "./pagination";

import { fetch } from "utils/fetch";
import cloneDeep from "lodash/cloneDeep"
import isArray from "lodash/isArray";

// import monent from "monent";

let formateQuery=(query)=>{
    let formateParam = {}
    
    for(var key in query){
        
        if(isArray(query[key])&&query[key].length>1&&query[key][0]){
            formateParam[key] = query[key].map((value)=>{return getYMD(value)}).join(",");
        }else if(isArray(query[key])){

        }
        else if(query[key]!='') {
            formateParam[key] = query[key];
        }
    }
    return formateParam;
}

let getYMD = (time=new Date())=>{
    console.log(time.getFullYear()+"-"+(Number(time.getMonth())+1)+"-"+time.getDate())
    return time.getFullYear()+"-"+formateTime((Number(time.getMonth())+1))+"-"+formateTime(time.getDate())+" "+formateTime(time.getHours())+":"+formateTime(time.getMinutes())+":"+formateTime(time.getSeconds());
}
let formateTime = (value)=>{
    return value<10?("0"+value):value
}
//
let getData = async query => {

    let formateParam = formateQuery(query);
    let result = false;
    console.log(":")
    console.log(formateParam);
    
    // 
    try{
        result = await fetch({
            url: "/user/addScoreList.do?"+toExcString(formateParam),
            method: "get",
            data: formateParam
        })
    }catch(err){
        result = false;
        
    }
    return result;
};

const toExcString = function (array, type = {
    ":": "=",
    ",": "&"
}) {

    let result = "";
    for (var temp in array) {
        result += temp + '=' + encodeURI(array[temp]) + "&"
    }
    return result.substring(-1, result.length - 1);
}

let defaultData = {
  header: {
    formInline: {
      userId: "",
      busType: "",
      busId: "",
      operator: "",
      scoreFlag: "",
      adjustTimes: [],
      validTimes: [],
      status: ""
    },
    pickerOptions2: {
      shortcuts: [
        {
          text: "",
          onClick(picker) {
            const end = new Date();
            const start = new Date();
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
            picker.$emit("pick", [start, end]);
          }
        },
        {
          text: "",
          onClick(picker) {
            const end = new Date();
            const start = new Date();
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
            picker.$emit("pick", [start, end]);
          }
        },
        {
          text: "",
          onClick(picker) {
            const end = new Date();
            const start = new Date();
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
            picker.$emit("pick", [start, end]);
          }
        }
      ]
    }
  },
  table: [],
  page: {
    page: 1,
    size: 10,
    total: 1
  }
};

export default {
  data() {
    let data = cloneDeep(defaultData);
    let searchInfo = Object.assign(
      {},
      data.header.formInline,
      data.page
    );
    data.searchInfo = searchInfo;
    delete data.searchInfo.total;

    let baseInfo = {
        loading:false
    }
    let info = Object.assign(baseInfo,data)
    return Object.assign(baseInfo,data);
  },
  mounted(){
      this.submit();
  },
  methods: {
    jumpPage: function(page) {
        this.$set(this.searchInfo,"page",page);
        this.submit();
    },
    setPageSize: function(pageSize) {
        this.$set(this.searchInfo,"size",pageSize);
        this.submit();
    },
    submit: async function(param) {
        let searchInfo = this.searchInfo;
        if(param){
            searchInfo = Object.assign({},this.searchInfo,param);
            
            this.searchInfo = searchInfo;
        }
       
        
        
        this.loading = true;
        let data = await getData(searchInfo);
        this.loading = false;
        if(data&&data.success===true){
            let dataInfo = data.data;
            this.table = dataInfo.items;
            this.page = {
                page:dataInfo.page,
                size:dataInfo.size,
                total:dataInfo.total
            }
        }

    }
  },
  components: {
    "list-header": Header,
    "list-table": Table,
    "list-page": Pageination
  }
};
</script>

<style lang="scss">
.container-list{
    display: flex;
    flex-direction: column;
    padding :10px 20px 5px;
    background-color: #fff;
}
.container-list-pagination{
        display: flex;
    justify-content: flex-end;
    padding: 10px 0px 5px;
}
</style>




